body {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    background-color: #fafafa;
}
* {
    box-sizing: border-box;
    -moz-box-sizing: border-box;
}
@page {
    size: A4;
    margin: 0;
}
@media print {
    html,
    body {
        width: 210mm;
        height: 297mm;
    }
    #main {
        margin: 0;
        padding: 0;
        border: initial;
        border-radius: initial;
        width: initial;
        min-height: initial;
        box-shadow: initial;
        background: initial;
        page-break-after: always;
    }
    #main .surat {
        margin: 0;
        box-shadow: none;
        border-radius: 0 !important;
    }

    #main .burger-btn,
    #sidebar,
    #buttonBack,
    header,
    .action {
        display: none !important;
    }
}

body #root .surat {
    width: 210mm;
    min-height: 297mm;
    padding: 10mm 20mm 15mm 20mm;
    margin: auto;
    border: 1px #d3d3d3 solid;
}
body #root .surat h3,
body #root .surat h2,
body #root .surat h1,
body #root .surat {
    font-family: "Times New Roman", Times, serif !important;
    color: #000 !important;
}
h3,
h2,
h1,
p {
    margin: 0;
    line-height: 1.5;
}
#kop-surat tr {
    width: 100%;
}

#kop-surat .logo,
#kop-surat .logo img {
    width: 25mm;
    height: 25mm;
}
#kop-surat .instansi {
    text-align: center;
    width: 100%;
}
#kop-surat .instansi h3 {
    font-size: 14pt;
}
#kop-surat .instansi h1 {
    font-size: 18pt;
}
#kop-surat .instansi p {
    font-size: 8pt;
}
#kop-surat .judul-surat {
    text-align: center;
    border-top: 2px solid black;
}
#kop-surat .judul-surat h2:nth-child(1) {
    margin-top: 10px;
    text-decoration: underline;
    font-weight: bold;
    font-size: 14pt;
}
#kop-surat .judul-surat h2:nth-child(2) {
    font-weight: normal;
    font-size: 14pt;
}
#isi-surat {
    margin-top: 20px;
    font-size: 12pt;
}
#isi-surat .alamat {
    height: 50px;
}
#isi-surat .paragraf {
    text-align: justify;
    text-indent: 40px;
}
#isi-surat .label p {
    margin-left: 60px;
    width: 200px;
}

#isi-surat .pembatas p {
    position: static;
    width: 10px;
}
#isi-surat .data-surat p {
    width: 300px;
    margin: 0;
}
#isi-surat .ket-surat p {
    text-align: justify;
    text-indent: 40px;
    margin-top: 20px;
}

#tanda-tangan {
    width: max-content;
    float: right;
    margin-top: 20px;
}

#tanda-tangan .lokasi-tanggal {
    text-indent: 20px;
}

#tanda-tangan .lurah {
    text-align: center;
    margin-top: 10px;
    font-weight: bold;
}
#tanda-tangan .ttd-lurah {
    margin-top: 60px;
    text-align: center;
    font-weight: bold;
}
